import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { ModalService } from 'src/app/core/services/modal.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { CustomerAgingService } from '../../../http/customer-aging.service';
import { InputModel} from '../../../models/customer-aging.model';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { parse } from 'path';
@Component({
  selector: 'app-customer-aging-dashboard-detail',
  templateUrl: './customer-aging-dashboard-detail.component.html',
  styleUrls:['./customer-aging-dashboard-detail.component.scss']
})
export class CustomerAgingDashboardDetailComponent extends BaseGrid implements OnInit {
  public gridDataList: any =[];
  getDataList(): void {
    throw new Error("Method not implemented.");
  }
  public mbLoadingEnabled = false;
  public mbIsLoading: boolean = false;
  public mobjUserData: any = {};
  public msScreenName: string = 'Customer Aging';
  //public msParmScreenName: string = 'Contra                                                                          ';
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public miAmountPrecision: number = 0;
  public miAmountPrecisionFormat: string = 'n' + this.miAmountPrecision;
  public autoCorrect: boolean = true;
  /**---Object Initialize---- */
  public marrGridData: Array<any> = [];
  public mobjData: InputModel = new InputModel;
  public mobjTotal: any = {};
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModaltitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';
  /**---END : Lookup realted field--- */
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  
  /**--------------START : SEGMENT--- */
  public msCaptionSegment1: string = '';
  public msCaptionSegment2: string = '';
  public msCaptionSegment3: string = '';
  public msCaptionSegment4: string = '';
  public msCaptionSegment5: string = '';
  public marrSegmentList: any = [];
  /**--------------START : SEGMENT--- */

  public msindexOfFromSegment : any ;
  public msindexOfToSegment : any;
  public msMsgData: any = {};
  public msgModal: boolean = false;

  /**-----Report parameter---- */
  public paramValueArray = [];
  public parameterValue: string = '';
  public msscreenModeUpdate = "I";
  public filterEnabled: boolean = false;
  public msDescription:string;
  public mbCommentDialog:boolean=false;
  public mdTotal:any;
  public mdBracket1:any;
  public mdBracket2:any;
  public mdBracket3:any;
  public mdBracket4:any;
  public msBracket1:string='';
  public msBracket2:string='';
  public msBracket3:string='';
  public msBracket4:string='';

  constructor(private route: ActivatedRoute, private router: Router,
    private modalservice: ModalService,
    private lookupService: LookupService,
    public toastMessageService: ToastMessageService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService, public lsService: LocalStorageService,
    private formService: CustomerAgingService,
    private activeRoute: ActivatedRoute,
  ) {  super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);}


  ngOnInit() {
    
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default formats from localStorage--- */
    let parrDefaultFormat = JSON.parse(localStorage.getItem("defaultFormats"));
    parrDefaultFormat.forEach(ele => {
      if (ele.Default_Key == "CURR_DECIMAL") {
        this.miAmountPrecision = Number(ele.Default_Value);
        this.miAmountPrecisionFormat = 'n' + this.miAmountPrecision;
      }
    });
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    let pdateCurrDate = new Date();
    this.setExportData({ 'pageName': this.msScreenName });
    this.exportData.translateFrom = '';
    this.bmlfGetData()
  }
 
  bmlfGetData() {
    //let Filter1,Filter2,Filter3,Filter4,Filter5,Filter6:any
    var paraData = JSON.parse(sessionStorage.getItem("CustomerAgingDetail"));
    var paraDataBracket = JSON.parse(sessionStorage.getItem("CustomerAgingBracketDetail"));
    this.msBracket1=paraDataBracket[0].BracketName
    this.msBracket2=paraDataBracket[1].BracketName
    this.msBracket3=paraDataBracket[2].BracketName
    this.msBracket4=paraDataBracket[3].BracketName


    this.mbIsLoading = true;
    this.marrGridData = [];
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: 'Default',       
      Filter1: paraData.Filter1,
      Filter2: paraData.Filter2,
      Filter3: paraData.Filter3,
      Filter4: paraData.Filter4,
      Filter5: paraData.Filter5,
      Filter6: paraData.Filter6,
      Filter7: paraData.Filter7,
      Filter8: paraData.Filter8,
    }
   this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false
      if (res.LookupData.length > 0) {
        this.marrGridData=res.LookupData;
         this.marrGridData.forEach(ele=>{
           ele.Total= Number(ele.Total).toFixed(this.miAmountPrecision);
           ele.Bracket1= Number(ele.Bracket1).toFixed(this.miAmountPrecision);
           ele.Bracket2= Number(ele.Bracket2).toFixed(this.miAmountPrecision);
           ele.Bracket3= Number(ele.Bracket3).toFixed(this.miAmountPrecision);
           ele.Bracket4= Number(ele.Bracket4).toFixed(this.miAmountPrecision);
           if (ele.AplDate!=null){
            ele.AplDate= moment(ele.AplDate).format(this.msCustomDateFormat.toUpperCase());
           }
           if (ele.DueDate!=null){
            ele.DueDate= moment(ele.DueDate).format(this.msCustomDateFormat.toUpperCase());
           }
         });
         this.mdTotal=0;
         this.mdBracket1=0;
         this.mdBracket2=0;
         this.mdBracket3=0;
         this.mdBracket4=0;

        let sumTotal: number = 0;
        let sumBracket1: number = 0;
        let sumBracket2: number = 0;
        let sumBracket3: number = 0;
        let sumBracket4: number = 0;
        this.marrGridData.forEach(a => sumTotal += Number(a.Total));
        this.marrGridData.forEach(a => sumBracket1 += Number(a.Bracket1));
        this.marrGridData.forEach(a => sumBracket2 += Number(a.Bracket2));
        this.marrGridData.forEach(a => sumBracket3 += Number(a.Bracket3));
        this.marrGridData.forEach(a => sumBracket4 += Number(a.Bracket4));

        this.mdTotal=Number(sumTotal).toFixed(this.miAmountPrecision);
        this.mdBracket1=Number(sumBracket1).toFixed(this.miAmountPrecision);
        this.mdBracket2=Number(sumBracket2).toFixed(this.miAmountPrecision);
        this.mdBracket3=Number(sumBracket3).toFixed(this.miAmountPrecision);
        this.mdBracket4=Number(sumBracket4).toFixed(this.miAmountPrecision);

      }else{
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('CUSTOMERAGING.NODATAFOUND'));
      }
    });
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  
  bmlfClose(): void {
    sessionStorage.removeItem('CustomerAgingDetail');
    sessionStorage.removeItem('CustomerAgingBracketDetail');
    this.router.navigate(['/main/accountsreceivable/customer-aging-dashboard']); 
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      //pobjFormRec.primeKey = this.mobjData.AccountGroup;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/' + this.msScreenName + '.pdf', '_blank');
  }

  bmlfRedirectTo(data){
    //this.router.navigate(['/main/generalladger/journal-enquiry'], { queryParams: { jrnKey: data.DocNo } }); //scr 160751
    this.router.navigate(['/main/generalladger/journal-enquiry'], { queryParams: { jrnKey: data.JrnlBtchNo } });
  }

bmlFilterSetting(){
  this.filterEnabled = !this.filterEnabled;
}

bmlfDescriptionModalGrid(event, index) {
  if (event == 'open') {
      this.msDescription = this.marrGridData[index].Narration;
      this.mbCommentDialog = true;
  } else if (event == 'close') {
    this.mbCommentDialog = false;
  }
}

}
