import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class ApplyReceiptService {
  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }

  public bmlfonGetReceiptDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReceiptDetails`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetUnAppliedAmount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetUnappliedAmountAR`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonGetUnAdjustedAmount(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUnadjustedAmountAR`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetInvoiceCreditDetails(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetInvoiceCreditDetails`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonProcessApplyReceipt(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/ProcessApplyReceipt`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetFinanceSeries(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetFinanceSeries`;
    return this.httpExt.post(url, reqdata, null);
  }

  public GetUnadjustedTCSAmount(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUnadjustedTCSAmountAR`;
    return this.httpExt.post(url, reqdata, null);
  }
}
