import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { AccountReceivableComponent } from './account-receivable.component';
import { ReceiptEntryListComponent } from './components/receipt-entry/receipt-entry-list/receipt-entry-list.component';
import { ReceiptEntryManageComponent } from './components/receipt-entry/receipt-entry-manage/receipt-entry-manage.component';
import { ARInvoiceListComponent } from './components/ar-invoice/ar-invoice-list/ar-invoice-list.component';
import { ARInvoiceManageComponent } from './components/ar-invoice/ar-invoice-manage/ar-invoice-manage.component';
import { ApplyReceiptComponent } from './components/apply-receipt/apply-receipt.component';
import { WriteoffCustomerDocumentsComponent } from './components/writeoff-customer-documents/writeoff-customer-documents.component';
import { CustomerAgingComponent } from './components/report/customer-aging/customer-aging.component';
import { CustomerAgingDashboardComponent } from './components/report/customer-aging/customer-aging-dashboard.component';
import { CustomerAgingDashboardDetailComponent } from './components/report/customer-aging/customer-aging-dashboard-detail.component';
import {ReceiptReportComponent} from './components/report/receipt-report/receipt-report.component';
import {ARInvoiceReportComponent} from './components/report/arinvoice-report/arinvoice-report.component';
import {CustomerCreditNoteReportComponent} from './components/report/customer-credit-note-report/customer-credit-note-report.component';
import {ApplyReceiptReportComponent} from './components/report/apply-receipt-report/apply-receipt-report.component'
const routes: Routes = [
    {
        path: '',
        component: AccountReceivableComponent,
        children: [
            {
                path: '', redirectTo: 'accountsreceivable', pathMatch: 'full'
            },
            { path: 'receipt-entry', component: ReceiptEntryListComponent, canActivate: [AuthGuard] },
            { path: 'receipt-entry/manage', component: ReceiptEntryManageComponent, canActivate: [AuthGuard] },
            { path: 'arinvoice', component: ARInvoiceListComponent, canActivate: [AuthGuard] },
            { path: 'customer-credit-note', component: ARInvoiceListComponent, canActivate: [AuthGuard] },
            { path: 'arinvoice/manage', component: ARInvoiceManageComponent, canActivate: [AuthGuard] },
            { path: 'customer-credit-note/manage', component: ARInvoiceManageComponent, canActivate: [AuthGuard] },
            { path: 'apply-receipt', component: ApplyReceiptComponent, canActivate: [AuthGuard] },
            { path: 'writeoff-customer-documents' , component: WriteoffCustomerDocumentsComponent, canActivate:[AuthGuard]},
            { path: 'customer-aging' , component: CustomerAgingComponent, canActivate:[AuthGuard]},
            { path: 'customer-aging-dashboard' , component: CustomerAgingDashboardComponent, canActivate:[AuthGuard]},
            { path: 'customer-aging-dashboard-detail' , component: CustomerAgingDashboardDetailComponent, canActivate:[AuthGuard]},
            { path: 'receipt-report' , component: ReceiptReportComponent, canActivate:[AuthGuard]},
            { path: 'ar-invoice-report' , component: ARInvoiceReportComponent, canActivate:[AuthGuard]},
            { path: 'customer-credit-note-report' , component: CustomerCreditNoteReportComponent, canActivate:[AuthGuard]},
            { path: 'apply-receipt-report' , component: ApplyReceiptReportComponent, canActivate:[AuthGuard]},
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AccountReceivableRoutingModule { }




