export class ProcessApplyReceiptModel {
    public Type: string;
    public OptionReceipt: boolean;
    public OptionCreditNo: boolean;
    public ReceiptNo: string;
    public CurrencyKey: string;
    public BatchSeries: string;
    public ApplyDate: Date;
    public ReceiptDate: Date;
    public ReceiptLineNo: number;
    public RemainingAmount: number;
    public TotalAppliedAmount: number;
    public TotalWriteOffAmount: number;
    public VendorCustomer: number;
    public Vend_CustKey: string;
    constructor() {
        this.Type = "Customer";
        this.OptionReceipt = true;
        this.OptionCreditNo = false;
        this.ReceiptNo = '';
        this.CurrencyKey = '';
        this.BatchSeries = '';
        this.ApplyDate = new Date();
        this.ReceiptDate = new Date();
        this.ReceiptLineNo = 0;
        this.RemainingAmount = 0;
        this.TotalAppliedAmount = 0;
        this.TotalWriteOffAmount = 0;
        this.VendorCustomer = 0;
        this.Vend_CustKey = '';
    }
}
export class ProcessApplyReceiptDetailsModel {
    public Sel: boolean;
    public InvoiceNo: string;
    public DueDate: Date;
    public Amount: number;
    public RemAmount: number;
    public ApplyAmount: number;
    public WriteOff: number;
    public JrnlBtchNo: string;
    public RecordPtr: number;
    public DocType: string;
    constructor() {
        this.Sel = false;
        this.InvoiceNo = '';
        this.DueDate = new Date();
        this.Amount = 0;
        this.RemAmount = 0;
        this.ApplyAmount = 0;
        this.WriteOff = 0;
        this.JrnlBtchNo = '';
        this.RecordPtr = 0;
        this.DocType = '';
    }
}
export class UnappliedAmount {
    public Vend_CustKey: string;
    public CurrencyKey: string;
    public Type: string;
    constructor() {
        this.Vend_CustKey = '';
        this.CurrencyKey = '';
        this.Type = 'Customer';
    }
}

export class ReceiptDetails {
    public ReceiptNo: string;
    constructor() {
        this.ReceiptNo = '';
    }
}

export class UnadjustedAmount {
    public ReceiptNo: string;
    public ReceiptLineNo: number;
    public OptionReceipt: boolean;
    public Type: string;
    constructor() {
        this.ReceiptNo = '';
        this.ReceiptLineNo = 0;
        this.OptionReceipt = false;
        this.Type = "Customer";
    }
}

export class InvoiceCreditDetails {
    public RefType: string;
    public Vend_CustKey: string;
    public CurrencyKey: string;
    public DocNo: string;
    public ReceiptDate: Date;
    public VendorCustomer: number;
    constructor() {
        this.RefType = '';
        this.Vend_CustKey = '';
        this.CurrencyKey = '';
        this.DocNo = '';
        this.ReceiptDate = new Date();
        this.VendorCustomer = 0;
    }
}
