import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable()
export class CustomerAgingService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfGETGeneralSqlData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETGeneralSqlData`;
        return this.httpExt.post<any>(url, headerData, null);
    } 
    public bmlfGetDefaultSQlData(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETCustomSqlData`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfGetLookUpData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetJournalEnquiryData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetJournalEnquiry`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetCompanyData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetCompanyInfo`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    
    public bmlfGetLedgerData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetLedgerDetails`;
        return this.httpExt.post<any>(url, headerData, null) ;
    }

    public bmlfGetTrialBalanceData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetTrialBalance`;
        return this.httpExt.post<any>(url, headerData, null) ;
    }
    public getReportParameter(screenName) {
        let json = { 'ScreenName': screenName }
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameter`;
        return this.httpExt.post(url, json, null);
      }
      public getReportParameterValue(screenName, param) {
        let json = { 'ScreenName': screenName, 'ParametreValue': param }
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameterValue`;
        return this.httpExt.post(url, json, null);
    
      }
      public saveReportParameters(controls, header) {
        let oData: any = {};
        oData.HeaderData = [...header];
        oData.Controls = [...controls];
        oData.Response = [];
        oData.Response.push({ "ResponseText": "", "ErrorCode": "" });
        let jData = JSON.stringify(oData);
        let tempjData = JSON.stringify(jData);
        let json = '{"CustomToken":' + tempjData + '}';
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveCustomParameter`;
        return this.httpExt.post(url, json, null);
      }
      public DeleteReportParameter(screenname, ParaValue) {
        let json = { 'ScreenName': screenname, 'ParametreValue': ParaValue }
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteCustomParameters`
        return this.httpExt.post(url, json, null)
    
      }
}

