  //---------------------------------------------------------------------------------------
  //Load
  ngOnInit() {

    this.objUtils.bmgfAddCustomLineToModel(this.mArrPaymentEntryGrid1Col, this.msscreenName, this.msVarReceiptEntryGrid).then(res => { });

    this.msReceiptNo = this.activeRoute.snapshot.queryParamMap.get("ReceiptNo") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("ReceiptNo");
    this.msStatus = this.activeRoute.snapshot.queryParamMap.get("Status") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("Status");
    this.msCopyRecord = this.activeRoute.snapshot.queryParamMap.get("CopyRecord");

    this.objUtils.getBMEScreePermission(this.msscreenName);
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.msRecUserID = this.localStorageService.getUserSession().UserId;
    this.mobjUserData = this.localStorageService.getUserSession();

    if (this.msReceiptNo != "") {
      this.bmlfGetDataOnLoad(this.msReceiptNo, this.msStatus);
    }
    else {
      this.msFormMode = "I";
      this.mbInsert = true;
      this.mbChange = false;
      this.mbDisabledPrimaryKey = false;
      this.mbCustomControl = true;
      this.mObjModel = Object.assign({}, this.mObjModelBlank);
      this.msCustVendName = this.objUtils.bmgfonTranslate('RECEIPTENTRY.CUSTOMERNAME');
      this.bmlfGetDefaultSeries();
      this.mObjModel.Status = "NEW";
      this.mObjModel.CurrConvFact = 1;
      this.bmlfonURL("", "");
      //scr 147586
      //setTimeout(function () { document.getElementById('txtReceiptNo').focus(); }, 10);
    }
    //this.mskeyValue = this.msReceiptNo;
    //this.mskeyValue = this.msReceiptNo + "" + this.msStatus;
    this.mskeyValue = this.msStatus + "" + this.msReceiptNo;
    //e-Sgnature -s
    this.mobjSignatureData = {
      scrrenName: this.msscreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();
    this.getTCSApplicable();

    this.SetGridColumns(this.msVarReceiptEntryGrid, this.mArrPaymentEntryGrid1Col);
  }
  //---------------------------------------------------------------------------------------
  //Add
  bmlfonAdd() {

    //check Write permission
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.msCallFor = "Add";
    let pbResult = this.bmlfonValidateFormData();

    if (pbResult == true) {
      if (!this.mbChange && !this.FieldExtender.mbChange && !this.customCtrl.mbChange) {
        this.bmlfonClearControl();
        this.mObjModel.Status = 'NEW';
        this.bmlfHandleControls('NEW');
      }
      else {
        // Ask for Yes No Cancel
        this.mbShowDialog = true;
        //this.msCallFor = "Add";
      }
    }
  }
  //---------------------------------------------------------------------------------------
