  //---------------------------------------------------------------------------------------
  bmlfonValidateFormData() {

    if (this.mObjModel.ReceiptNo.trim() == "") {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.SELECTSERIES')));
      setTimeout(function () { document.getElementById('txtReceiptNo').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.PaymentType == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.PAYMENTYPEBLANK')));
      setTimeout(function () { document.getElementById('cmbPaymentType').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.PaymentType == "B" && this.mObjModel.PaymentMode.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.PAYMENTMODEBLANK')));
      setTimeout(function () { document.getElementById('txtPaymentMode').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.ReceiptAcct.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.PAYMENTACCTBLANK')));
      setTimeout(function () { document.getElementById('txtReceiptAcct').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Customer == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.VENDCUSTACCTERR')));
      setTimeout(function () { document.getElementById('txtCustomer').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Currency_Key.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.CURRENCYKEYBLANK')));
      setTimeout(function () { document.getElementById('txtCurrencyKey').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.TotalAmount == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.ZEROTOTALAMOUNT')));
      return false;
    }

    //--------------Grid Validation----------------- 
    let errRow: string = '';
    let errFiled: string = '';
    let pirowindex: number = 0;
    let pirowindexRefType: number = 0;
    let pirowAmtChk: number = 0;
    if (this.mArrPaymentEntryGrid.length > 0) {
      this.mArrPaymentEntryGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.Amount == 0) {
            errRow += '' + errRow;
            errFiled = 'Amount';
          } else if (ele.RefType == '') {
            errRow += '' + errRow;
            errFiled = 'RefType';
            pirowindexRefType = pirowindex;
          } else if ((ele.DocAmount < (ele.Amount + ele.WriteOffAmt)) && (ele.RefType == 'Against Ref' || ele.RefType == 'New Ref') && this.mObjModel.CustVendMisType != 2) {
            errRow += '' + errRow;
            errFiled = 'AmountChk';
            pirowAmtChk = pirowindex;
          }
        }
        ++pirowindex;
      });

      if (errFiled == "RefType") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.ROWENTRYBLANK') + String(pirowindexRefType + 1)));
        return false;
      } else if (errFiled == "Amount") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.ZEROROWAMOUNT')));
        return false;
      } else if (errFiled == "AmountChk") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.VALIDAMOUNT')));
        return false;
      }
    }
    //--------------Grid Validation-----------------

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }

    return true;
  }
  //---------------------------------------------------------------------------------------
  bmlfonisEmpty(value) {
    return (typeof value === "undefined" || value === null || value === '');
  }
  //---------------------------------------------------------------------------------------
  bmlfonDeleteDialogAction(action) {

    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.msFormMode == "I" && (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true)) {
        this.mbShowDeleteDialog = false;
        this.bmlfonClearControl();
        return;
      }
      else if (this.mbInsert && this.mbChange) {
        this.mbShowDeleteDialog = false;
        let deleteKey = "Receipt Entry  " + this.mObjModel.ReceiptNo;
        this.FieldExtender.bmgfDeleteData(deleteKey)
        this.bmlfonClearControl();
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('PRICEGROUP.MSGDELETE'));
        return false;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            ReceiptNo: this.mObjModel.ReceiptNo.trim()
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };

      this.ReceiptEntryService.bmlfonDeleteReceiptEntry(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError('Server Error');
          return false;
        }

        if (res.Response[0].ErrorCode == "") {

          if (res.Response[0].ResponseText.search("Reference") != -1) {
            this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
            this.mbShowDeleteDialog = false;
            return;
          }
          else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
            this.mbShowDeleteDialog = false;
            this.objToastService.notifySuccess((this.objUtils.bmgfonTranslate('COMMON.MSG.DELETE')));
            this.bmlfonClearControl();
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
            this.mbShowDeleteDialog = false;
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText)
          this.mbShowDeleteDialog = false;
        }

      })
    }
  }
