  //---------------------------------------------------------------------------------------
  bmlfonAddReceiptEntryLine() {

    //validation
    if (this.mObjModel.Customer == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.VENDCUSTACCTERR')));
      return false;
    }
    let errRow: string = '';
    let errFiled: string = '';
    let pirowindex: number = 0;
    let pirowindexRefType: number = 0;
    if (this.mArrPaymentEntryGrid.length > 0) {
      this.mArrPaymentEntryGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.Amount == 0) {
            errRow += '' + errRow;
            errFiled = 'Amount';
          }
          if (ele.RefType == '') {
            errRow += '' + errRow;
            errFiled = 'RefType';
            pirowindexRefType = pirowindex;
          }
        }
        ++pirowindex;
      });

      if (errFiled == "RefType") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.ROWENTRYBLANK') + String(pirowindexRefType + 1)));
        return false;
      } else if (errFiled == "Amount") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.ZEROROWAMOUNT')));
        return false;
      }
    }

    //--------------End VAlidation------------

    const pobjDataCopy: any = Object.assign({}, this.mObjModelGrid1);
    //----------Row No - S
    let piLastIndex;
    if (this.mArrPaymentEntryGrid.length > 0) {
      piLastIndex = this.mArrPaymentEntryGrid.reduce((max, obj) => obj.RowNo > max ? obj.RowNo : max, this.mArrPaymentEntryGrid[0].RowNo);
    } else {
      piLastIndex = this.mArrPaymentEntryGrid.length;
    }
    pobjDataCopy.RowNo = Number(piLastIndex) + 1;
    //----------Row No - E

    pobjDataCopy.Recuserid = this.msRecUserID;
    pobjDataCopy.Flag = "A";
    if (this.mObjModel.CustVendMisType == 2) {//Miscellaneous
      pobjDataCopy.RefType = "New Ref";
      pobjDataCopy.isRefTypeDisabled = true;
      pobjDataCopy.isDocAmountDisabled = true;
      pobjDataCopy.isDiscountDisabled = true;
      pobjDataCopy.isWriteOffDisabled = true;
    }

    this.mArrPaymentEntryGrid.push(pobjDataCopy);

    // if(this.mObjModel.VendMiscType == 2){//Miscellaneous
    //   this.mArrPaymentEntryGrid1Col[1].Type = 'textReadOnly'; //Refrence Type Readonly
    // }

    this.mbVendorCustDisabled = true;
    this.mbCurrencyKeyDisabled = true;
    this.mbVendMiscTypeDisabled = true;
  }
  //---------------------------------------------------------------------------------------
  bmlfonRefTypeOnChangesFn(psFieldName, piIndex, getFlag) {

    this.msLabelKey = '';
    if (getFlag == false) {
      this.mArrPaymentEntryGrid[piIndex].DocNo = '';
      this.mArrPaymentEntryGrid[piIndex].DueDate = new Date();
      this.mArrPaymentEntryGrid[piIndex].Amount = 0;
      this.mArrPaymentEntryGrid[piIndex].AmountBankCurr = 0;
      this.mArrPaymentEntryGrid[piIndex].DocAmount = 0;
      this.mArrPaymentEntryGrid[piIndex].DocAmtBankCurr = 0;
      this.mArrPaymentEntryGrid[piIndex].Discount = 0;
      this.mArrPaymentEntryGrid[piIndex].DiscountBankCurr = 0;
      this.mArrPaymentEntryGrid[piIndex].BankCharges = 0;
      this.mArrPaymentEntryGrid[piIndex].BankChargesBankCurr = 0;
      this.mArrPaymentEntryGrid[piIndex].WriteOffAmt = 0;
      this.mArrPaymentEntryGrid[piIndex].WriteOffBankCurr = 0;
      this.mArrPaymentEntryGrid[piIndex].TDSAmt = 0;
    }
    if (this.mObjModel.CustVendMisType != 2) {//Not Miscellaneous
      {
        if (this.mArrPaymentEntryGrid[piIndex].RefType == 'New Ref') {
          this.mArrPaymentEntryGrid[piIndex].isDocAmountDisabled = true;
          this.mArrPaymentEntryGrid[piIndex].isDiscountDisabled = true;
          this.mArrPaymentEntryGrid[piIndex].isWriteOffDisabled = true;
          if (this.mObjModel.CustVendMisType == 1) {
            this.msLabelKey = 'Receipt Entry.PONO';
          } else {
            this.msLabelKey = 'Receipt Entry.SONO';
          }

        } else if (this.mArrPaymentEntryGrid[piIndex].RefType == 'Against Ref') {
          this.mArrPaymentEntryGrid[piIndex].isDocAmountDisabled = true; //readOnly
          this.mArrPaymentEntryGrid[piIndex].isDiscountDisabled = false; //Write
          this.mArrPaymentEntryGrid[piIndex].isWriteOffDisabled = false; //Write
          if (this.mObjModel.CustVendMisType == 1) {
            this.msLabelKey = 'Receipt Entry.VDN';
          } else {
            this.msLabelKey = '';
          }
        } else {
          this.mArrPaymentEntryGrid[piIndex].isDocAmountDisabled = false; //Wrtie
          this.mArrPaymentEntryGrid[piIndex].isDiscountDisabled = true; //readOnly
          this.mArrPaymentEntryGrid[piIndex].isWriteOffDisabled = true; //readOnly
        }
      }
    } else {//Miscellaneous
      this.mArrPaymentEntryGrid[piIndex].isDocAmountDisabled = true;
      this.mArrPaymentEntryGrid[piIndex].isDiscountDisabled = true;
      this.mArrPaymentEntryGrid[piIndex].isWriteOffDisabled = true;
    }
  }
