  //---------------------------------------------------------------------------------------
  bmlfonDeleteGrid1Line() {

    this.mbChange = true;
    //DeleteLineReceiptEntry
    if (this.mArrPaymentEntryGrid[this.miGrid1DeleteLineIndex].Flag == 'A') {
      this.mArrPaymentEntryGrid.splice(this.miGrid1DeleteLineIndex, 1);
      if (this.mArrPaymentEntryGrid.length == 0) {
        this.mbVendorCustDisabled = false;
        this.mbCurrencyKeyDisabled = false;
        this.mbVendMiscTypeDisabled = false;
      }
    } else {
      //
      // this.mArrPaymentEntryGrid.forEach(ele => {

      // });

      let reqData: any = {
        JSON: JSON.stringify({
          DetailData: [{
            ReceiptNo: this.mObjModel.ReceiptNo,
            RowNo: this.mArrPaymentEntryGrid[this.miGrid1DeleteLineIndex].RowNo
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.mbIsLoading = true;
      this.objService.bmlfonDeleteLineReceiptEntry(reqData).subscribe(res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mArrPaymentEntryGrid.splice(this.miGrid1DeleteLineIndex, 1);
          if (this.mArrPaymentEntryGrid.length == 0) {
            this.mbVendorCustDisabled = false;
            this.mbCurrencyKeyDisabled = false;
            this.mbVendMiscTypeDisabled = false;
          }
        }
      });
    }//end else
    this.mArrPaymentEntryGrid.forEach(element =>{
      this.mnTotalNetAmount =+ Number(element.DocAmount) 
    })
  }
