  bmlfAmountChanges(rowIndex, itemField) {

    this.mbChange = true;
    if (itemField == 'Amount') {
      if (this.mArrPaymentEntryGrid[rowIndex].Amount != 0) {
        this.mArrPaymentEntryGrid[rowIndex].AmountBankCurr = (this.mArrPaymentEntryGrid[rowIndex].Amount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        //this.bmlfonGetPaymentTDSAmount(rowIndex);
        if (this.mArrPaymentEntryGrid[rowIndex].RefType == "Against Ref") {
          this.bmlfonGetReceiptDiscountAmount(rowIndex);
        }
      } else {
        this.mArrPaymentEntryGrid[rowIndex].AmountBankCurr = 0;
      }
      if (this.mbTCSApplicable) {

        let result = (this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User8 / this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].DocAmount) * this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].Amount;
        if (result <= this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User8) {
          this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User9 = result;
        }
        else {
          this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User9 = this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User8;
        }
      }
    } else if (itemField == 'AmountBankCurr') {
      if (this.mArrPaymentEntryGrid[rowIndex].AmountBankCurr != 0) {
        this.mArrPaymentEntryGrid[rowIndex].Amount = (this.mArrPaymentEntryGrid[rowIndex].AmountBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].Amount = 0;
      }

    }
    else if (itemField == 'DocAmount') {
      if (this.mArrPaymentEntryGrid[rowIndex].DocAmount != 0) {
        this.mArrPaymentEntryGrid[rowIndex].DocAmountBankCurr = (this.mArrPaymentEntryGrid[rowIndex].DocAmount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        this.mArrPaymentEntryGrid[rowIndex].DocAmtBankCurr = (this.mArrPaymentEntryGrid[rowIndex].DocAmount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].DocAmountBankCurr = 0;
        this.mArrPaymentEntryGrid[rowIndex].DocAmtBankCurr = 0;
      }

    }
    else if (itemField == 'DocAmtBankCurr') {
      if (this.mArrPaymentEntryGrid[rowIndex].DocAmtBankCurr != 0) {
        this.mArrPaymentEntryGrid[rowIndex].DocAmount = (this.mArrPaymentEntryGrid[rowIndex].DocAmtBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].DocAmount = 0;
      }
    }
    else if (itemField == 'Discount') {
      if (this.mArrPaymentEntryGrid[rowIndex].Discount != 0) {
        if (this.mArrPaymentEntryGrid[rowIndex].Amount < this.mArrPaymentEntryGrid[rowIndex].Discount) {
          this.mArrPaymentEntryGrid[rowIndex].Discount = this.mArrPaymentEntryGrid[rowIndex].Amount;
        }
        this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr = (this.mArrPaymentEntryGrid[rowIndex].Discount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr = 0;
      }
    }
    else if (itemField == 'DiscountBankCurr') {
      if (this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr != 0) {
        this.mArrPaymentEntryGrid[rowIndex].Discount = (this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        if (this.mArrPaymentEntryGrid[rowIndex].Amount < this.mArrPaymentEntryGrid[rowIndex].Discount) {
          this.mArrPaymentEntryGrid[rowIndex].Discount = this.mArrPaymentEntryGrid[rowIndex].Amount;
          this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr = (this.mArrPaymentEntryGrid[rowIndex].Discount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        }
      } else {
        this.mArrPaymentEntryGrid[rowIndex].Discount = 0;
      }
    }
    else if (itemField == 'BankCharges') {
      if (this.mArrPaymentEntryGrid[rowIndex].BankCharges != 0) {
        this.mArrPaymentEntryGrid[rowIndex].BankChargesBankCurr = (this.mArrPaymentEntryGrid[rowIndex].BankCharges * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].BankChargesBankCurr = 0;
      }
    }
    else if (itemField == 'BankChargesBankCurr') {
      if (this.mArrPaymentEntryGrid[rowIndex].BankChargesBankCurr != 0) {
        this.mArrPaymentEntryGrid[rowIndex].BankCharges = (this.mArrPaymentEntryGrid[rowIndex].BankChargesBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].BankCharges = 0;
      }
    }
    else if (itemField == 'WriteOffAmt') {
      if (this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt != 0) {
        //this.mArrPaymentEntryGrid[rowIndex].WriteOffAmtBankCurr = (this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        this.mArrPaymentEntryGrid[rowIndex].WriteOffBankCurr = (this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].WriteOffBankCurr = 0;
      }
    }
    else if (itemField == 'WriteOffAmtBankCurr') {
      if (this.mArrPaymentEntryGrid[rowIndex].WriteOffAmtBankCurr != 0) {
        //this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt = (this.mArrPaymentEntryGrid[rowIndex].WriteOffAmtBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
        this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt = (this.mArrPaymentEntryGrid[rowIndex].WriteOffBankCurr / (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      } else {
        this.mArrPaymentEntryGrid[rowIndex].WriteOffAmt = 0;
      }
    }
    else if (itemField == 'TDSAmt') {
      if (this.mArrPaymentEntryGrid[rowIndex].Amount < this.mArrPaymentEntryGrid[rowIndex].TDSAmt) {
        this.mArrPaymentEntryGrid[rowIndex].TDSAmt = this.mArrPaymentEntryGrid[rowIndex].Amount;
      }
    }
    this.bmlfCalculateTotal();
  }
