  //---------------------------------------------------------------------------------------
  bmlfCalculateTotal() {
    this.mObjModel.TotalAmount = 0;

    this.mnTotalTermsDiscount = 0;
    this.mnTotalBankCharges = 0;
    this.mnTotalWriteOffAmount = 0;
    this.mnTotalTDSAmount = 0;
    this.mnTotalNetAmount = 0;

    this.mnTotalAmountBankCurr = 0;
    this.mnTotalTermsDiscountBankCurr = 0;
    this.mnTotalBankChargesBankCurr = 0;
    this.mnTotalWriteOffAmountBankCurr = 0;
    this.mnTotalTDSAmountBankCurr = 0;
    this.mnNetAmountBankCurr = 0;

    //this.mObjModel.Amount1099 = 0;
    this.mArrPaymentEntryGrid.forEach(ele => {
      this.mObjModel.TotalAmount += ele.Amount;
      this.mnTotalAmountBankCurr += ele.AmountBankCurr;
      this.mnTotalTermsDiscount += Number(ele.Discount);
      this.mnTotalTermsDiscountBankCurr += Number(ele.DiscountBankCurr);
      this.mnTotalBankCharges += Number(ele.BankCharges);
      this.mnTotalBankChargesBankCurr += Number(ele.BankChargesBankCurr);
      this.mnTotalWriteOffAmount += Number(ele.WriteOffAmt);
      this.mnTotalWriteOffAmountBankCurr += Number(ele.WriteOffBankCurr);

      this.mnTotalTDSAmount += Number(ele.TDSAmt);
      //this.mnTotalTDSAmountBankCurr += Number(ele.TDSAmt * this.mObjModel.CurrConvFact);
      this.mnTotalTDSAmountBankCurr += Number(ele.TDSAmt);

    });

    this.mnTotalNetAmount = Number(Number(this.mObjModel.TotalAmount) - Number(this.mnTotalTermsDiscount + this.mnTotalBankCharges + this.mnTotalTDSAmount));
    this.mnNetAmountBankCurr = Number(Number(this.mnTotalAmountBankCurr) - Number(this.mnTotalTermsDiscountBankCurr + this.mnTotalBankChargesBankCurr + this.mnTotalTDSAmountBankCurr));

  }
  //---------------------------------------------------------------------------------------
  bmlfonCurrConvFactChange() {
    this.mbChange = true;
    if (this.mArrPaymentEntryGrid.length > 0) {
      this.mArrPaymentEntryGrid.forEach(ele => {
        if (ele.Amount != 0) {
          ele.AmountBankCurr = (ele.Amount * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
        }
        if (ele.DocAmount != 0) {
          ele.DocAmountBankCurr = (ele.DocAmount * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
          ele.DocAmtBankCurr = (ele.DocAmount * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
        }
        if (ele.Discount != 0) {
          ele.DiscountBankCurr = (ele.Discount * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
        }
        if (ele.BankCharges != 0) {
          ele.BankChargesBankCurr = (ele.BankCharges * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
        }
        if (ele.WriteOffAmt != 0) {
          ele.WriteOffAmtBankCurr = (ele.WriteOffAmt * ((this.mObjModel.CurrConvFact == undefined || this.mObjModel.CurrConvFact == 0) ? 1 : this.mObjModel.CurrConvFact));
        }
      });
    }

  }

