  // // Post Button
  bmlfReceiptEntryPost() {

    // if (this.mbPostToolButton == true) {
    //   return;
    // }
    if (this.mObjModel.PostingDate > new Date()) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.VALIDPOSTINGDATE')));
      return false;
    }

    //SCR 
    if (this.mbInsert) {
      this.msCallFor = "Post";
      let pbResult = this.bmlfonValidateFormData();
      if (pbResult == false) {
        return;
      }
      // this.mbShowDialog = true;
      return;
    }

    if (this.mbChange || this.FieldExtender.mbChange || this.customCtrl.mbChange) {
      this.msCallFor = "POST";
      this.mbShowDialog = true;
      return;
    };

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReceiptNo: this.mObjModel.ReceiptNo
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonPostReceiptEntry(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.Response[0].ErrorCode == "") {
        //this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
        this.msReport = res.Response[0].ResponseText;
        this.msAfterProcessMessage.screenName = this.msscreenName;
        this.msAfterProcessMessage.Data = res.Response[0].ResponseText;
        this.mbDescriptionDialog = true;
        this.mbChange = false;
        this.bmlfGetDataOnLoad(this.mObjModel.ReceiptNo, 'Both');
      } else {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      }
    });
  }
