  //---------------------------------------------------------------------------------------
  bmlfGetCurrencyConversion(currSource) {


    let bnkcurId = this.mObjModel.BankCurrency;
    let acctCurrId = this.mObjModel.Currency_Key;

    let getCurrRate = '';
    if (currSource == 'BankCurrency') {
      getCurrRate = this.mObjModel.BankCurrency;
    } else {
      getCurrRate = this.mObjModel.Currency_Key;
    }

    if (bnkcurId == acctCurrId && bnkcurId != '' && acctCurrId != '') {
      this.mObjModel.CurrConvFact = 1;
      this.mbCurrencyConversion = true;
      this.bmlfAmountChangesOnAccountChange();
      return;
    } else {
      this.mbCurrencyConversion = false;
      let pdtReceiptDate = moment(this.mObjModel.ReceiptDate).format(CommonConstants.gsServerDateFormat.toUpperCase()); //this.objUtils.bmlfGetFormatedDateWithoutTime(this.mObjModel.ReceiptDate)
      let pobjReqData = {
        ScreenName: 'ReceiptEntryComponent',
        LookUpKey: 'CurrencyConvAsonDate',
        Filter1: getCurrRate,
        Filter2: pdtReceiptDate
      }
      this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
        if (res.LookupData.length > 0) {
          //Conversion Logic
          if (currSource == 'BankCurrency') {
            this.msBankCurrencyRate = res.LookupData[0].SellRate;//BuyRate;
            this.mdtBnkcurrConDate = res.LookupData[0].Curr_date;
          } else {
            this.msCurrencyKeyRate = res.LookupData[0].SellRate;//BuyRate;
            this.mdtAcctcurrConDate = res.LookupData[0].Curr_date;
          }
        }
        // else {
        //   this.msBankCurrencyRate = 1; //scr 146884
        //   this.mdtAcctcurrConDate = null;
        // }

        if (this.msHomeCurrency == this.mObjModel.BankCurrency) {
          this.mObjModel.CurrConvFact = this.msCurrencyKeyRate;
          this.mObjModel.CurrConvDate = (this.mdtAcctcurrConDate == undefined ? new Date() : new Date(this.mdtAcctcurrConDate));
        } else if (this.msHomeCurrency == this.mObjModel.Currency_Key && this.msBankCurrencyRate != 0) {
          if (this.msBankCurrencyRate == 0) {
            this.msBankCurrencyRate = 1;
          }
          this.mObjModel.CurrConvFact = (1 / this.msBankCurrencyRate);
          this.mObjModel.CurrConvDate = (this.mdtBnkcurrConDate == undefined ? new Date() : new Date(this.mdtBnkcurrConDate));
        } else if (this.mObjModel.BankCurrency != this.mObjModel.Currency_Key) {
          //if (this.mObjModel.BankCurrency != '' && this.mObjModel.Currency_Key != '' && this.msBankCurrencyRate != 0) {//scr 146884
          if (this.mObjModel.BankCurrency != '' && this.mObjModel.Currency_Key != '') {
            if (this.msBankCurrencyRate == 0) {
              this.mObjModel.CurrConvFact = this.msCurrencyKeyRate;
            } else {
              this.mObjModel.CurrConvFact = (this.msCurrencyKeyRate / this.msBankCurrencyRate);
            }
            this.mObjModel.CurrConvDate = (this.mdtAcctcurrConDate == undefined ? new Date() : new Date(this.mdtAcctcurrConDate));
          }
        } else {
          this.mObjModel.CurrConvFact = 0;
        }
        if (this.mObjModel.CurrConvFact == 0) {
          this.mObjModel.CurrConvFact = 1;
        }
        this.bmlfAmountChangesOnAccountChange();
      });

    }
  }
