  bmlfReversalModal(event) {

    if (event == 'open') {
      this.mdtReversalDate = new Date(this.mObjModel.ReceiptDate);
      this.mbReversalDialog = true;
    } else if (event == 'close') {
      this.mbReversalDialog = false;
    } else if (event == 'yes') {
      if (this.mdtReversalDate < this.mObjModel.ReceiptDate) {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RECEIPTENTRY.MSG.REVERSALDATECHK')));
        setTimeout(function () { document.getElementById('dtReversalDate').focus(); }, 10);
        return false;
      }
      this.mbReversalDialog = false;
      this.bmlfReveralService();
    }

  }
  //-------------------------------------------------------------------
  bmlfReveralService() {

    let revDate;
    revDate = this.objUtils.bmlfGetFormatedDateWithoutTime(this.mdtReversalDate);
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReceiptNo: this.mObjModel.ReceiptNo,
          ReverseDate: revDate
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonReverseReceiptEntry(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.Response[0].ErrorCode == "") {
        //this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
        this.mbChange = false;
        this.msReport = res.Response[0].ResponseText;
        this.msAfterProcessMessage.screenName = this.msscreenName;
        this.msAfterProcessMessage.Data = res.Response[0].ResponseText;
        this.mbDescriptionDialog = true;
        this.bmlfGetDataOnLoad(this.mObjModel.ReceiptNo, 'Both');
      } else {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      }
    });
  }
