  bmlfonGetReceiptDiscountAmount(rowIndex) {

    let revDate;
    revDate = this.objUtils.bmlfGetFormatedDateWithoutTime(this.mdtReversalDate);
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ARAP: "AR",
          DocumentNo: this.mArrPaymentEntryGrid[rowIndex].DocNo,
          DueDate: this.mArrPaymentEntryGrid[rowIndex].DueDate,
          ReceiptDate: this.mObjModel.ReceiptDate,
          PaidAmount: this.mArrPaymentEntryGrid[rowIndex].Amount,
          Discount: "0.00",
        }],
        Response: [{ ResponseText: "", ErrorCode: "" }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonGetReceiptDiscountAmount(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.Response[0].ErrorCode == "") {
        let pdDiscount: number = 0;
        pdDiscount = Number(res.HeaderData[0].Discount);
        this.mArrPaymentEntryGrid[rowIndex].Discount = pdDiscount
        this.mArrPaymentEntryGrid[rowIndex].DiscountBankCurr = (pdDiscount * (this.mObjModel.CurrConvFact == 0 ? 1 : this.mObjModel.CurrConvFact));
      }
    });
  }

  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msscreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

