  // begin tcs 04/mar/2021 deepak     
  getTCSApplicable() {

    let parrDefaultAppSetup = JSON.parse(localStorage.getItem("DistributionParameter"));
    if (parrDefaultAppSetup) {
      parrDefaultAppSetup.forEach((ele) => {
        if (ele.DEFAULT_KEY == 'TCS Applicable') {
          if (ele.DEFAULT_VALUE == 'YES' || ele.DEFAULT_VALUE == 'Yes') {
            this.mbTCSApplicable = true;
          }
          this.bmlfTCSShowColumn();
        } else if (ele.DEFAULT_KEY == 'TDS_IMPLEMENTED') {
          if (ele.DEFAULT_VALUE == 'YES' || ele.DEFAULT_VALUE == 'Yes') {
            this.mbTDSApplicable = true;
          }
          this.bmlfTCSShowColumn();
        }
      });

    }
  }
  // end tcs 04/mar/2021 deepak 
  bmlfTCSShowColumn() {

    this.mArrPaymentEntryGrid1Col.forEach(col => {
      if (col.Field == "User8") {
        col.Display = this.mbTCSApplicable;
      }
      if (col.Field == "User9") {
        col.Display = this.mbTCSApplicable;
      }
      if (col.Field == "TDSAmt") {
        col.Display = this.mbTDSApplicable;
      }
    });
  }

  public GetPaymentTCSDueAmount(PaymentNo: string, DocumentNo: number, VendorCustomer: string): void {
    let calledFor = '';
    if (this.mObjModel.CustVendMisType == 1) {
      calledFor = 'Voucher';
    }
    else {
      calledFor = 'Invoice';
    }

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          "CallFor": calledFor,
          "DocumentNo": DocumentNo,
          "ReceiptNo": PaymentNo,
          "VendorCustomerKey": VendorCustomer,
          "TCSDueAmt": '0.00'
        }], Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.GetReceiptTCSDueAmount(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mArrPaymentEntryGrid[this.mnumLineItemRowIndex].User8 = Number(res.HeaderData[0]['TCSDueAmt']);
      }
    }, err => {
      console.log(err);
    });
  }
