  //------------------------------------------------------------------------
  bmlfCommonCardAction(psAction, event) {
    if (psAction == 'removeCard') {
      if (event == 'Yes') {
        this.mObjModel.PaymentDocNo = '';
        this.msForEncrptCardNumber = '';
        this.mbChange = true;
      }
      this.mbRemoveCardno = false;
    }
    if (psAction == 'enterCardNo') {
      if (event == 'Yes') {
        this.mObjModel.PaymentDocNo = this.msCardNumber;
        this.msForEncrptCardNumber = this.msCardNumber;
        this.msCardNumber = '';
        this.mbChange = true;
      }
      this.mbEnterCardno = false;
    }
  }
