bmlfonValidateFormData() {

  let pbAgeBracket:boolean ; 
  let i;
  for( i =0; i< this.marrAgeBracketGridData.length;i++) {
    if (this.marrAgeBracketGridData[i].BracketNo !=4){ 
      if (Number(this.marrAgeBracketGridData[i].FromDay) > Number(this.marrAgeBracketGridData[i].UpToDay)) {
        pbAgeBracket= true;
        break;
      }
    }
}

if (pbAgeBracket){
  i=i+1;
  let psMsg=this.utilService.bmgfonTranslate('CUSTOMERAGING.AGEBRACKETVALIDATION')  + i;
  this.toastMessageService.notifyError(psMsg);
  return false;
}
  return true;
}
bmlfChangeGrid(rowIndex,psKey,psValue){
  this.marrAgeBracketGridData[rowIndex].BracketName=this.marrAgeBracketGridData[rowIndex].FromDay + '-' + this.marrAgeBracketGridData[rowIndex].UpToDay
  if (rowIndex==2){
    this.marrAgeBracketGridData[rowIndex].BracketName=this.marrAgeBracketGridData[rowIndex].FromDay + '-' + this.marrAgeBracketGridData[rowIndex].UpToDay
    this.marrAgeBracketGridData[3].FromDay=this.marrAgeBracketGridData[rowIndex].UpToDay+1;
    this.marrAgeBracketGridData[3].BracketName='>' + this.marrAgeBracketGridData[rowIndex].UpToDay
  }
}
bmlfChangeGroupBy(psGroupBy){
  if(psGroupBy=='Salesperson'){
    this.mobjData.Currency='H';
  }
}


