  public static bmgfGetUnPost(psInvFlag) {
    let sqalFilter = "'+CAST(COUNT(Tranno) as nvarchar(10))+'";
    let json = '{ "Field": "UnPost", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from ARHDR where invfalg = '" + psInvFlag + "'  and ISNULL(UpdatedtoFin,0) in(0)"
    return SqlQuery;
  }

  public static bmgfGetPost(psInvFlag) {
    let sqalFilter = "'+CAST(COUNT(Tranno) as nvarchar(10))+'";
    let json = '{ "Field": "Post", "Count": ' + sqalFilter + ', "Type": "String", "FirstKey": "Status", "FirstValue": "POSTED","SecondValue" : "", "colorClass":"light-blue-bg" }';
    let SqlQuery = "select  '" + json + "' RecCount from ARHDR where invfalg = '" + psInvFlag + "'  and ISNULL(UpdatedtoFin,0) Not in(0)"
    return SqlQuery;
  }

