  //---------------------------------------------------------------------------------------
  bmlfGetDataOnLoad(msSOInvoice, msTranno) {

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          SOInvoice: msSOInvoice
        }],
        ScreenName: [{
          CallType: this.msscreenName
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonGetARInvoice(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.HeaderData.length > 0) {
        this.msFormMode = "U";
        this.mbDisabledPrimaryKey = true;
        this.mObjModel = Object.assign({}, this.mObjModelBlank);
        this.mObjModel = res.HeaderData[0];

        //Begin Format date/time columns in customization grid field to remove "T"  
        this.objUtils.bmlfTransformCustomFieldsGet(res.HeaderData[0])
        //end Format date/time columns in customization grid field 

        this.mbCustomControl = true;


        this.mObjModel.Invdate = new Date(res.HeaderData[0].Invdate);
        //this.mnSubTotal =  (res.HeaderData[0].DocTot) - (res.HeaderData[0].TotalTax);



        if (this.mObjModel.UpdatedtoFin == '0') {
          this.msStatus = 'NEW';
          this.mbDiabledAllFields = true;
        } else if (this.mObjModel.UpdatedtoFin == '2') {
          this.msStatus = 'POSTED';
          this.mbDiabledAllFields = true;
        } if (this.mObjModel.UpdatedtoFin == '3') {
          this.msStatus = 'VOID';
          this.mbDiabledAllFields = true;
        }


        this.mArrVendorDebitGrid = [];
        if (res.DetailData.length > 0) {

          let parrGridData = res.DetailData;
          parrGridData.forEach(ele => {
            //ele.DueDate = new Date(ele.DueDate);
            //ele.AddModify = 'M';
          })

          this.mArrVendorDebitGrid = parrGridData; //res.DetailData;
          if (res.TaxData.length > 0) {// for VAT Tax
            //this.mArrTaxDetails = res.TaxData;
            this.mArrTaxData = res.TaxData;
          }


        }

        this.bmlfHandleControls(this.mObjModel.UpdatedtoFin);//Disable Enable on Status
        this.bmlfGetLookupDes(this.mObjModel);
        this.mbDisabledPrimaryKey = true;
        this.bmlfCalculateTotal();
        this.bmlfCheckEsignApplicableOnForm();/// Bug 192437
      }
      else {
        this.msFormMode = "I";
        this.mbDisabledPrimaryKey = false;
      }
      if (this.msCopyRecord == "Y") {
        this.msFormMode = "I";
        this.mObjModel.SOInvoice = ""; // clear the primary key
        this.mObjModel.UpdatedtoFin = '0';
        this.msStatus = 'NEW';
        this.mbDisabledPrimaryKey = false;
        this.mbChange = true;
        this.bmlfonSetFormMode("I", "Copy");
        this.bmlfCalculateTotal();
        //SCR 147559
        //setTimeout(function () { document.getElementById('txtSOInvoice').focus(); }, 10);
      }
      //   if (this.msCallFor == "Post"){
      //     this.bmlfVoucherPost1();
      //     return
      //  }
    })
    this.bmlfonURL(msSOInvoice, msTranno)
  }
