  bmlfonURL(psKey, psVouchNo) {
    if (psKey == "") {
      this.msURL = "/main/accountsreceivable/customer-credit-note/manage";
    } else {
      this.msURL = "/main/accountsreceivable/customer-credit-note/manage?DebitNoteNo=" + psKey + '&Vouchno=' + psVouchNo;
    };

  }
