  //---------------------------------------------------------------------------------------
  bmlfGetInputDesColumn(psField): string {
    if (psField == 'Credit Note') {
      return 'SOInvoice';
    } else if (psField == 'Finance Series') {
      return 'SOInvoice';
    } else if (psField == 'Customer') {
      return 'Custkey';
    } else if (psField == 'Ship To') {
      return 'Branchkey';
    } else if (psField == 'Purchase Order') {
      return 'Pono';
    } else if (psField == 'Debit Note') {
      return 'Invno';
    } else if (psField == 'Currency') {
      return 'Currency_Key';
    }

  }
  //---------------------------------------------------------------------------------------
  bmlfonValidateFormData() {
    if (this.mObjModel.SOInvoice.trim() == "") {
      if (this.mbSearchEvent == true) {
        return false;
      }

      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.SELECTSERIES')));
      setTimeout(function () { document.getElementById('txtSOInvoice').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Custkey == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.CUSTOMERCHECK')));
      setTimeout(function () { document.getElementById('txtCustkey').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.Invdate == undefined) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.DATEBLANKCHK')));
      setTimeout(function () { document.getElementById('dateInvdate').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.DocTot == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.ZEROTOTALAMOUNT')));
      return false;
    }
    //--------------Grid Validation-----------------
    let errRow: string = '';
    let errFiled: string = '';
    if (this.mArrVendorDebitGrid.length > 0) {
      this.mArrVendorDebitGrid.forEach(ele => {
        if (ele.errFiled != '') {
          if (ele.QtyOrder == 0) {
            errRow += '' + errRow;
            errFiled = 'QtyOrder';
          }
          if (ele.Description == '') {
            errRow += '' + errRow;
            errFiled = 'Description';
          }
        }
      });

      if (errFiled == "Description") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.ROWENTRYBLANK')));
        return false;
      } else if (errFiled == "QtyOrder") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.ZEROTOTALAMOUNT')));
        return false;
      }
    }
    //--------------Grid Validation-----------------

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }

    return true;
  }
