  //---------------------------------------------------------------------------------------
  bmlfCloseLookupModal(pobjData): void {
    //Grid Start
    if (pobjData.field == "Tax") {
      let parrFilterData: any = [];
      parrFilterData = pobjData.gridData.filter(obj => obj.Txratekey.toUpperCase() == pobjData.searchValue.toUpperCase());
      if (parrFilterData.length == 0) {
        this.mArrVendorDebitGrid[this.mnumLineItemRowIndex].Tax = '';
      }
    }
    if (pobjData.field == "Accounts") {
      let parrFilterData: any = [];
      parrFilterData = pobjData.gridData.filter(obj => obj.Acct.toUpperCase() == pobjData.searchValue.toUpperCase());
      if (parrFilterData.length == 0) {
        this.mArrVendorDebitGrid[this.mnumLineItemRowIndex].AccountKey = '';
      }
    }
    if (pobjData.field == "Non Inventory Item") {
      let parrFilterData: any = [];
      if( pobjData.searchValue != undefined){
        parrFilterData = pobjData.gridData.filter(obj => obj.Description.toUpperCase() == pobjData.searchValue.toUpperCase());
        if (parrFilterData.length == 0 ||  pobjData.searchValue == undefined ) {
          this.mArrVendorDebitGrid[this.mnumLineItemRowIndex].Description = '';
        }
      }else{
        this.mArrVendorDebitGrid[this.mnumLineItemRowIndex].Description = '';
      }

    
    }



    //Grid End
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey && pobjData.searchValue != null) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0 || pobjData.searchValue == null) {
        let psModelField = this.bmlfGetModelField(pobjData.field, pobjData.type);
        this.mObjModel[psModelField] = '';
      }
      document.getElementById(pobjData.eleId).focus();
    }
  }
