  //Close
  bmlfonClose() {
    if (!this.mbChange && !this.FieldExtender.mbChange) {
      if (this.msscreenName == "Customer Credit Note") {
        this.router.navigate(['/main/accountsreceivable/customer-credit-note']);
      } else if (this.msscreenName == "AR Invoice") {
        this.router.navigate(['/main/accountsreceivable/arinvoice']);

      }
    }
    else {
      this.mbShowDialog = true;
      this.msCallFor = "FormClose";
    }
  }

  //---------------------------------------------------------------------------------------
  bmlfAmountChanges(rIndex, iField) {

    this.mbChange = true;

    //Call CalculateLineTax --S
    //,

    if (iField == "BasePrice" || iField == "QtyOrder") {
      this.mArrVendorDebitGrid[rIndex]['Extension'] = Number(this.mArrVendorDebitGrid[rIndex]['QtyOrder']) * Number(this.mArrVendorDebitGrid[rIndex]['BasePrice']);
      this.mArrVendorDebitGrid[rIndex]['LineNet'] = Number(this.mArrVendorDebitGrid[rIndex]['QtyOrder']) * Number(this.mArrVendorDebitGrid[rIndex]['BasePrice']) + Number(this.mArrVendorDebitGrid[rIndex]['TaxAmount']);
    }

    let parrLineItemRow = [];

    let parrDataList = this.mArrVendorDebitGrid.map(x => Object.assign({}, x));

    parrDataList.map(ele => {
      if (ele.SOLineNo == this.mArrVendorDebitGrid[rIndex]['SOLineNo']) {
        ele.NONINVITEMKEY = ele.Description;
        ele.QtyRem = Number(ele.QtyOrder)//.toFixed(this.miQtyPrecision);//code change according to mahesh for tax calcualtion

        ele.PriceUOM = ele.PO_Unit;
        ele.DispBasePrice = Number(ele.BasePrice)//.toFixed(this.miPricePrecision);

        ele.QtyOrder = Number(ele.QtyOrder);//.toFixed(this.miQtyPrecision);
        ele.BasePrice = Number(ele.BasePrice);//.toFixed(this.miPricePrecision);

        ele.Price = Number(ele.BasePrice)//.toFixed(this.miPricePrecision);
        //ele.Extension = Number(ele.Extension).toFixed(this.miPricePrecision);
        ele.Extension = (Number(ele.BasePrice) * Number(ele.QtyOrder));//.toFixed(this.miAmountPrecision);

        ele.TaxRate = Number(ele.TaxRate)//.toFixed(this.miAmountPrecision);
        ele.Tax1 = Number(ele.Tax1)//.toFixed(this.miAmountPrecision);
        ele.Tax2 = Number(ele.Tax2)//.toFixed(this.miAmountPrecision);
        ele.Tax3 = Number(ele.Tax3)//.toFixed(this.miAmountPrecision);
        ele.Tax4 = Number(ele.Tax4)//.toFixed(this.miAmountPrecision);
        ele.CustReqDate = '';

        ele.TxClcL1 = Number(ele.TxClcL1)////.toFixed(this.miAmountPrecision);
        ele.TxClcL2 = Number(ele.TxClcL2)//.toFixed(this.miAmountPrecision);
        ele.TxClcL3 = Number(ele.TxClcL3)//.toFixed(this.miAmountPrecision);
        ele.TxClcL4 = Number(ele.TxClcL4)//.toFixed(this.miAmountPrecision);
        //ele.TaxAmount = Number(ele.TaxAmount);//.toFixed(0.00);
        //ele.TaxAmount = Number(ele.TaxAmount)//.toFixed(this.miAmountPrecision)
        //147560
        ele.TaxAmount = Number(ele.TaxAmount).toString()//.toFixed(this.miAmountPrecision)
        ele.Tranno = Number(ele.Tranno)
        if (ele.TaxIncInPr == null || ele.TaxIncInPr == "") { ele.TaxIncInPr = "0" }
        if (ele.AddModify != 'A') {
          ele.AddModify = 'M';
        }
        parrLineItemRow.push(ele);
      }

    });

    this.mObjModel.Tranno = this.mObjModel.Tranno ==''?  Number(0) : Number(this.mObjModel.Tranno);
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ ...this.mObjModel }],
        DetailData: parrLineItemRow,//parrDataList,
        TaxData: [],
        ScreenName: [
          { "CallType": this.msscreenName }
        ],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };

    this.objService.bmlfonGetARInvoiceCalculateLineTax(reqData).subscribe(res => {
      if (res == null) {
        this.objToastService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.mArrVendorDebitGrid[rIndex]['BasePrice'] = res.DetailData[0].BasePrice;
        this.mArrVendorDebitGrid[rIndex]['DispBasePrice'] = res.DetailData[0].BasePrice;
        this.mArrVendorDebitGrid[rIndex]['Price'] = res.DetailData[0].BasePrice;

        this.mArrVendorDebitGrid[rIndex]['Extension'] = Number(res.DetailData[0].Extension);
        //this.mArrVendorDebitGrid[rIndex]['LineNet'] = Number(res.DetailData[0].LineNet + res.DetailData[0].TaxAmount);
        this.mArrVendorDebitGrid[rIndex]['LineNet'] = Number(Number(res.DetailData[0].BasePrice) * Number(res.DetailData[0].QtyOrder)) + Number(res.DetailData[0].TaxAmount);
        this.mArrVendorDebitGrid[rIndex]['TaxAmount'] = Number(res.DetailData[0].TaxAmount);

        this.mArrVendorDebitGrid[rIndex]['TxClcL1'] = Number(res.DetailData[0].TxClcL1);
        this.mArrVendorDebitGrid[rIndex]['TxClcL2'] = Number(res.DetailData[0].TxClcL2);
        this.mArrVendorDebitGrid[rIndex]['TxClcL3'] = Number(res.DetailData[0].TxClcL3);
        this.mArrVendorDebitGrid[rIndex]['TxClcL4'] = Number(res.DetailData[0].TxClcL4);

        if (res.DetailData[0].AddModify != 'A') {
          this.mArrVendorDebitGrid[rIndex]['AddModify'] = 'M';
        }

        if (this.msAppTaxType == 'VAT') {

          let piRow: number = this.mArrVendorDebitGrid[rIndex].SOLineNo;
          for (let pi = this.mArrTaxData.length - 1; pi >= 0; pi--) {
            if (this.mArrTaxData[pi].DocLinNo.toString() == piRow.toString()) {
              this.mArrTaxData.splice(pi, 1);//Delete exists Tax line
            }
          }
          if (res.TaxData.length > 0) {//Add Tax line
            res.TaxData.forEach(ele => {
              this.mArrTaxData.push(ele);
            })
          };
        }

        this.bmlfCalculateTotal();
      } else {
        console.log("Error in Service: ", res)
      }
    });
    //Call CalculateLineTax --E

  }
  //---------------------------------------------------------------------------------------
  bmlfCalculateTotal() {

    console.log('in calculate total', this.mObjModel.TotalTax)
    this.mObjModel.DocTot = 0;
    //this.mnSubTotal = 0;
    this.mObjModel.SubTotal = 0;
    this.mObjModel.TotalTax = 0;
    this.mObjModel.TotalDiscount = 0;
    this.mnTDSAmount = 0;
    this.mArrVendorDebitGrid.forEach(ele => {
      if (ele.TaxIncInPr == "1") {
        this.mObjModel.SubTotal = Number(this.mObjModel.SubTotal) + Number(ele.Extension - ele.TaxAmount);
      } else {
        this.mObjModel.SubTotal = Number(this.mObjModel.SubTotal) + Number(ele.Extension);
      }
      //this.mObjModel.SubTotal += ele.Extension;
      if (ele.TDSAmt == undefined) { ele.TDSAmt = 0 };
      this.mnTDSAmount = Number(this.mnTDSAmount) + Number(ele.TDSAmt);
      //this.mObjModel.TxClc += ele.TaxAmount;
      this.mObjModel.TotalTax = Number(this.mObjModel.TotalTax) + Number(ele.TaxAmount);

    });
    //Invoice Total
    this.mObjModel.DocTot = Number(this.mObjModel.SubTotal) + Number(this.mObjModel.TotalTax);


  }
