  bmlfVoucherPost() {

    //check Write permission
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    let pbResult = this.bmlfonValidateFormData();
    if (pbResult == false) {
      return;
    }

    //SCR 145609
    if (this.mbInsert) {
      this.msCallFor = "Post";
      this.mbShowDialog = true;
      return;
    }

    if (!this.mbChange && !this.FieldExtender.mbChange && !this.customCtrl.mbChange) {
      this.bmlfVoucherPost1();
    } else {
      this.msCallFor = "Post";
      this.mbShowDialog = true;
    }
  }

  bmlfVoucherPost1() {

    //scr 145609
    // if (this.mbPostToolButton == true) {
    //   return;
    // }
    if (new Date(this.mObjModel.Invdate) > new Date()) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('ARINVOICE.MSG.VALIDPOSTINGDATE')));
      setTimeout(function () { document.getElementById('dateInvdate').focus(); }, 10);
      return false;

    }
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          SOInvoice: this.mObjModel.SOInvoice
        }],
        ScreenName: [{
          CallType: this.msscreenName
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonPostARInvoice(reqData).subscribe(res => {
      if (res == null) {
        this.objToastService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.msMsgData.screenName = this.msscreenName;
        this.msMsgData.Data = res.Response[0].ResponseText;
        this.mbBulkResponseView = true;
        this.mbChange = false;
        this.bmlfGetDataOnLoad(this.mObjModel.SOInvoice, this.mObjModel.Tranno);
      }
      else {
        this.objToastService.notifyError(res.Response[0].ResponseText);
        this.mbShowDeleteDialog = false;
      }
    });
  }

