  // Get Lookup Description
  bmlfGetLookupDes(pobjData) {
    let parrSetKeyData = [
      { KeyCode: 'CUSTKEY', KeyValue1: pobjData.Custkey, KeyValue2: 'Custkey', KeyDescription: '' },
      { KeyCode: 'SHIPTO', KeyValue1: pobjData.Custkey, KeyValue2: pobjData.Shipkey, KeyDescription: '' },
      { KeyCode: 'CURRENCY', KeyValue1: pobjData.Currency_Key, KeyValue2: 'Currency_Key', KeyDescription: '' },
      { KeyCode: 'COUNTRY', KeyValue1: pobjData.Custcntry, KeyValue2: 'Custcntry', KeyDescription: '' },
      { KeyCode: 'COUNTRY', KeyValue1: pobjData.Shipcntry, KeyValue2: 'Shipcntry', KeyDescription: '' },
      { KeyCode: 'SLSPERSON', KeyValue1: pobjData.Salespkey, KeyValue2: 'Salespkey', KeyDescription: '' },
      { KeyCode: 'CUSTCLASS', KeyValue1: pobjData.Custclass, KeyValue2: 'Custclass', KeyDescription: '' },
      { KeyCode: 'FOB', KeyValue1: pobjData.Fobkey, KeyValue2: 'Fobkey', KeyDescription: '' },
      { KeyCode: 'COMMENTS', KeyValue1: pobjData.Commentkey, KeyValue2: 'Commentkey', KeyDescription: '' },
      { KeyCode: 'SHIPVIA', KeyValue1: pobjData.Shipviakey, KeyValue2: 'Shipviakey', KeyDescription: '' },
      { KeyCode: 'TERRITORY', KeyValue1: pobjData.Territkey, KeyValue2: 'Territkey', KeyDescription: '' },
      { KeyCode: 'TERMS', KeyValue1: pobjData.Termskey, KeyValue2: 'Termskey', KeyDescription: '' },
      { KeyCode: 'LOCATION', KeyValue1: pobjData.Location, KeyValue2: 'Location', KeyDescription: '' }


    ]
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msscreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };

    this.userService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.LookupData.length) {
        res.LookupData.forEach((ele) => {
          if (ele.KeyValue2 == 'Custkey') {
            //this.mObjModel.Custname = ele.KeyDescription;
            this.msCustname = ele.KeyDescription;
          } else if (ele.KeyCode == 'SHIPTO') {
            this.mObjModel.Shipname = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Currency_Key') {
            //this.msShipCountryName = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Custcntry') {
            this.msCustomerCountryName = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Shipcntry') {
            this.msShipCountryName = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Salespkey') {
            this.msCustomerSalespDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Custclass') {
            this.msCustclassDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Fobkey') {
            this.msCustomerFobDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Commentkey') {
            this.msCustomerCommentDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Shipviakey') {
            this.msCustomerShipviaDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Territkey') {
            this.msCustomerTerritoryDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Termskey') {
            this.msCustomerTermsDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Location') {
            this.msCustomerLocationDesc = ele.KeyDescription;
          }
        });
      }
    }, err => {
      console.log(err);
    });
  }
  //---------------------------------------------------------------------------------
  bmlfonDialogAction(action) {

    if (action == 'No') {
      this.mbShowDialog = false;
      this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        if (this.msscreenName == "Customer Credit Note") {
          this.router.navigate(['/main/accountsreceivable/customer-credit-note']);
        } else if (this.msscreenName == "AR Invoice") {
          this.router.navigate(['/main/accountsreceivable/arinvoice']);
        }
      }
      else if (this.msCallFor == "Add") {
        this.bmlfonClearControl();
      }
      else if (this.msCallFor == "Copy") {
        this.bmlfonSetFormMode("I", "Copy");
      }
      else if (this.msCallFor == "Search") {
        this.bmlfOpenLookupModal('ARInvoiceSearch', 'AR Invoice', 'ARInvoiceList', 'click', '', 'custom', 'txtSOInvoice');
        return;
      } else if (this.msCallFor == "Post") {
        this.bmlfGetDataOnLoad(this.mObjModel.SOInvoice, this.mObjModel.Tranno);
      }
      else {
        return;
      }
    }// if no
    else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Copy") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Search") {
      this.msCallFor = "SaveSearch";
      this.bmlfonSubmitData();
      this.mbShowDialog = false;
      //this.bmlfOpenLookupModal('ARInvoiceSearch', 'AR Invoice', 'ARInvoiceList', 'click', '', 'custom', 'txtSOInvoice');
    } else if (action == 'Yes' && this.msCallFor == 'Post') {
      this.mbShowDialog = false;
      this.mbChange = true;
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    }
    else {
      this.mbShowDialog = false;
    }
   // setTimeout(function () { document.getElementById('txtSOLineNo').focus(); }, 10);
  }
