  // Get filter Order
  bmlfonGetFilterOrderData(filterType) {
    this.mbIsLoading = true;
    let pobjReqData: any = {};
    // if (filterType == 'OverDueDate') {
    //   pobjReqData.SqlQuery = Grid1Constants.bmgfGetSoOverDueData();
    // } else if (filterType == 'ShipToWeek') {
    //   pobjReqData.SqlQuery = Grid1Constants.bmgfGetSoShipToWeekData();
    // } else if (filterType == 'CloseOrd') {
    //   pobjReqData.SqlQuery = Grid1Constants.bmgfGetClosedOrdData();
    // } else if (filterType == 'ProcessedOrd') {
    //   pobjReqData.SqlQuery = Grid1Constants.bmgfGetProcessedOrdData();
    // }

    this.userFieldService.getCustomSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      this.gridDataList = [];
      if (res.LookupData.length) {
        res.LookupData.forEach(ele => {
          ele.OrderDate = new Date(ele.OrderDate);
          ele.PromiseDate = new Date(ele.PromiseDate);
          ele.RequestedDate = new Date(ele.RequestedDate);
          ele.TragetShipdate = new Date(ele.TragetShipdate);
          //ele.RecDate = new Date(ele.RecDate);
          ele.OrderNo = ele.SalesOrderNo;
          // 22/dec/2020 scr no.146777 deepakporwal
          if (this.msScreenName == "Customer Credit Note") {
            ele.RecDate = new Date(ele.RecDate);
          }
        });
        this.gridDataList = res.LookupData;
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfonClearFilterEvent() {
    this.gridDataList = this.marrOrginalGridList;
    this.mbClearFilter = false;
  }

  bmlfonFilterEvent(pobjData) {
    
    this.mbClearFilter = true;
    this.mbIsLoading = true;

    if (pobjData.FirstKey == "Status" && pobjData.Field == "Post") {
      let parrData = [];
      parrData = this.marrOrginalGridList.filter(x => {
        return x[pobjData.FirstKey] == pobjData.FirstValue;
      });
      this.gridDataList = parrData;
      this.mbIsLoading = false;
    } else { //UnPost
      let parrData = [];
      parrData = this.marrOrginalGridList.filter(x => {
        return x[pobjData.FirstKey] != pobjData.FirstValue;
      });
      this.gridDataList = parrData;
      this.mbIsLoading = false;
    }
  }

  bmlfGetEntry(EntryType) {
    
    this.msUnposted = EntryType == "Post" ? "Posted" : "Unposted";
    if (this.msUnposted == "Unposted") {
      this.mbUnPosted = true;
    } else if (this.msUnposted == "Posted") {
      this.mbUnPosted = false;
    }
    this.getDataList('filtered');
  }

  bmlfmbCommonAlertDialog(event) {   
      this.mbCommonAlertDialog = false;    
  }
  //print 
  // Single Print
  bmlfonPrint(dataItem: any) {
    
    let psPosted: string = 'POSTED';
    if (dataItem.Status == 'NEW' || dataItem.Status == 'HOLD') {//|| dataItem.Status == 'VOID'
      psPosted = 'UNPOSTED';
    }
    try {
      let ReportScreenName;
      let printReqParam:any={};
      if(this.msScreenName == "Customer Credit Note"){
        ReportScreenName='Customer Credit Note Report';
         printReqParam = {
          "ReportName": ReportScreenName,
          "CustomerKeyFrom": dataItem.CustKey,
          "CustomerKeyTo": dataItem.CustKey,
          "Criteria": psPosted,
          "CreditNoteNoFrom": dataItem.SOInvoice,
          "CreditNoteNoTo": dataItem.SOInvoice,
          "NumberFrom": dataItem.Tranno,
          "NumberTo": dataItem.Tranno,
          "DateFrom": moment( this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "DateTo":  moment( this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": false,
          "ListingVariable": ""
        }
      }
      else{
        ReportScreenName='AR Invoice Report';
         printReqParam = {
          "ReportName": ReportScreenName,
          "CustomerKeyFrom":dataItem.CustKey,
           "CustomerKeyTo":dataItem.CustKey,
           "Criteria":psPosted,
           "ARInvoiceFrom":dataItem.SOInvoice,
           "ARInvoiceTo":dataItem.SOInvoice,
          "DateFrom": moment( this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "DateTo":  moment( this.objUtil.bmlfSetDateAsDateFormat(dataItem.Date)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": false,
          "ListingVariable": ""
        }
      }
     
      this.utilService.bmgfReportAction(printReqParam, ReportScreenName).then((value) => {
        this.maarraySelected =[];
        this.getDataList();
      });

    } catch (e) {
      console.log(e);
    }
  }

  // Multiple Print
  onActionPrint() {
          
    let pnReceiptType: number = 0;
    let psPrintAll: string = '';
    let piCnt: number = 0;
    let pdDateFrom = new Date();
    let pdDateTo = new Date();
    this.maarraySelected.forEach((ele) => {
      if (piCnt > 0) {
        psPrintAll =psPrintAll + '' + ele.SOInvoice;
        pdDateTo = ele.Date;
      } else {
        psPrintAll = ele.SOInvoice;
        pdDateFrom = ele.Date;
      }
      ++piCnt;
    });
    try {
      let ReportScreenName;
      let printReqParam:any={};
      if(this.msScreenName == "Customer Credit Note"){
        ReportScreenName='Customer Credit Note Report';
        printReqParam = {
          "ReportName": ReportScreenName,
          "CustomerKeyFrom": '',
          "CustomerKeyTo": '',
          "Criteria": this.msUnposted.toUpperCase(),
          "CreditNoteNoFrom": '',
          "CreditNoteNoTo": '',
          "NumberFrom": '',
          "NumberTo": '',
          "DateFrom": '',//moment( this.objUtil.bmlfSetDateAsDateFormat(pdDateFrom)).format(CommonConstants.gsServerDateFormat),
          "DateTo": '',//moment( this.objUtil.bmlfSetDateAsDateFormat(pdDateTo)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": true,
          "ListingVariable": psPrintAll
        }
      }
      else{
        ReportScreenName='AR Invoice Report';
        printReqParam = {
          "ReportName": ReportScreenName,
          "CustomerKeyFrom":'',
           "CustomerKeyTo":'',
           "Criteria":this.msUnposted.toUpperCase(),
           "ARInvoiceFrom":'',
           "ARInvoiceTo":'',
          "DateFrom": '',//moment( this.objUtil.bmlfSetDateAsDateFormat(pdDateFrom)).format(CommonConstants.gsServerDateFormat),
          "DateTo": '',//moment( this.objUtil.bmlfSetDateAsDateFormat(pdDateTo)).format(CommonConstants.gsServerDateFormat),
          "ListingFlag": true,
          "ListingVariable": psPrintAll
        }
      }
      
      this.utilService.bmgfReportAction(printReqParam, ReportScreenName).then((value) => {
        this.maarraySelected =[];
        this.getDataList();
      });

    } catch (e) {
      console.log(e);
    }
  }
    

